.Orngard {
	padding: 50px;
}

.orngardHeader {
	font-family: "Poiret One", sans-serif;
	color: darkgray;
	font-size: 84px;
	display: inline-block;
}
.IPA {
	display: inline-block;
	font-family: "Poiret One", sans-serif;
	font-size: 32px;
	margin-left: 50px;
}
.orngardSubheader {
	font-family: 'Miss Fajardose', cursive;
	color: #383838;
	font-size: 64px;
}
.rerouteCard {
	display: inline-flex;
	padding: 22px;
	margin: 50px 14px;
	background-color: #383838;
	border-radius: 4px;
	box-shadow: 6px 14px 36px #383838;
}
.rerouteText {
	display: inline-table;
	font-size: 36px;
	font-family: "Poiret One", Geneva, sans-serif;
	color: white;
}
.rerouteButton {
	display: inline-table;
	bottom: 0;	
	margin: 12px;
	padding: 16px;
	font-family: "Dorsa", Verdana, sans-serif;
	font-size: 72px;
	border-radius: 4px;
	background-color: rgba(107, 209, 194, 1);
	color: white;
}
.rerouteButton:hover {
	background-color: rgba(41, 133, 119, 1);
}
/*============================
==== WEDDING ================
============================*/
.wedding {
	color: gray;
	margin: 0;
	padding: 0;
	width: 100%;
	overflow: hidden;
}

.cartographer {
	position: relative;
	margin: 0;
	padding: 0;
	width: 100%;
	display: inline-flex;
}

/*============================
==== NAVBAR ================
============================*/

.navbar {
	width: 100%;
	display: inline-table;
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  transition: .5s;
	position: fixed;
	top: 0;
	left: 0;


}
.leftTextBox {
	font-family: 'Dorsa', sans-serif;
	font-family: 'Dawning of a New Day', cursive;
	font-family: 'Meddon', cursive;
	font-family: 'Oleo Script Swash Caps', cursive;
	font-family: 'Megrim', cursive;
	font-family: 'Reem Kufi', sans-serif;
	font-family: 'Miss Fajardose', cursive;
	color: white;
	transition: .5s;
	display: inline-flex;
	float: left;

}
.navButton {
	background-color: rgba(107, 209, 194, 1);
	-webkit-columns: darkgray;
	        columns: darkgray;
	border-radius: 6px;
	font-size: 48px;
	font-family: 'Dorsa', sans-serif;
	font-weight: 600;
	margin: 6px;
	padding: 12px;
	max-width: 120px;
	display: inline-block;
}
.navButton:hover {
	cursor: pointer;
	color: white;
	background-color: rgba(22, 70, 63, 1);
  box-shadow: 10px 10px 18px #888888;

}
.navUser {
	float: right;
	margin: 12px;
	padding: 4px;
	border: solid white 2px;
	border-radius: 3px;
	display: inline-flex;
	
	float: right;
	transition: .5s;

	background-color: rgba(184, 184, 184, 0.8);
	color: rgba(46, 46, 46, 1);
	opacity: .8 !important;
}
.navUser:hover {
	background-color: rgba(184, 184, 184, 1);
	color: rgba(18, 18, 18, 1);
	cursor: pointer;
}
.navButtonText {
	font-family: 'Reem Kufi', Georgia, sans-serif;
	display: inline-table;
}

.mIcons {
	display: inline-table;
	vertical-align: middle;
}

.sidebarTrigger {
	float: right;
	padding: 4px;
	margin: 12px;
	background-color: rgba(120, 120, 120, .8);
	color: white;	
	border: solid white 2px;
	border-radius: 3px;
	display: inline-flex;
	transition: .5s;
	opacity: .8;
}
.sidebarTrigger:hover {
	background-color: rgba(120, 120, 120, 1);
	cursor: pointer;
}

